import React, { useState, useEffect } from "react";
import { Switch, Route, Redirect } from "react-router-dom";

// component
import TopNavigation from "../../components/topNavigation/topNavigation";
import Info from "./info/info";
import Need from "./info/info";
import Profile from "./profile/profile";
import Setting from "./setting/setting";

// apis
import apis from "../../apis";

export default function Home({ location, setIsLogin }) {

  const [postType, setPostType] = useState({ id: 1, typeName: "食" }); // 七種文章類型物件: 食衣住行...
  const [myInfo, setMyInfo] = useState({}); // 自己的帳戶資訊物件
  const [closePost, setCloseTypePost] = useState([])

  useEffect(() => {
    // 取得自己的帳戶資訊
    const getMyInfo = async () => {
      const res = await apis.UserApi.getUser();
      setMyInfo(res.data);
    };

    // // 取得七種文章類型
    // const getTypeAll = async () => {
    //   const res = await apis.TypeApi.getTypes();
    //   setPostType(res.data[0]);
    // };

    getMyInfo(); // 取得自己的帳戶資訊
    // getTypeAll(); // 取得七種文章類型
    return () => {};
  }, []);

  return (
    <div className="home">
      {/* 上方選單bar */}
      <TopNavigation
        myInfo={myInfo}
        location={location}
        postType={postType}
        setpostType={setPostType}
        setIsLogin={setIsLogin}
        closePost={closePost}
        setCloseTypePost={setCloseTypePost}
      ></TopNavigation>

      {/* 空格 */}
      <div className="d-flex mt-6"></div>

      <Switch>
        {/* 路徑: 資訊頁面 */}
        <Route path="/home/info">
          <Info
            myInfo={myInfo}
            postType={postType}
            setpostType={setPostType}
            location={location}
            closePost={closePost}
          ></Info>
        </Route>
        {/* 路徑: 需求頁面 */}
        <Route path="/home/Need">
          <Need
            myInfo={myInfo}
            postType={postType}
            setpostType={setPostType}
            location={location}
            closePost={closePost}
          ></Need>
        </Route>
        {/* 路徑: 個人頁面 */}
        <Route path="/home/profile">
          <Profile
            myInfo={myInfo}
            postType={postType}
            setPostType={setPostType}
          ></Profile>
        </Route>
        {/* 路徑: 設定頁面 */}
        <Route path="/home/setting">
          <Setting myInfo={myInfo}></Setting>
        </Route>
        {/* 路徑: 直接導向頁面 */}
        <Route
          path="/home"
          render={() => <Redirect to="/home/info?topic=food" />}
        ></Route>
      </Switch>
    </div>
  );
}
