import React from "react";
import { Link } from "react-router-dom";

/**
 * 上方 bar 搜尋區域
 */
export default function NavSearch() {

  // 搜尋貼文
  const searchPost = (event) => {
    event.preventDefault();
    console.log(event.target.value);
  };

  return (
    <div className="nav-search-container d-flex ai-center">
      {/* LOGO 區域 */}
      <Link to="/" className="logo" href="#">
        <img
          src={require("../../../assets/image/logo_pure.png")}
          alt="CIRAS-logo"
        />
      </Link>

      {/* 搜尋貼文UI */}
      <form action="" className="search d-flex bg-gray-3 p-1 ai-center">
        <i className="iconfont icon-search fw-bold fs-lg p-1"></i>
        <input
          className="search-input bg-none fs-lgs ml-2"
          type="text"
          placeholder="搜尋貼文"
          onKeyPress={(event) =>
            event.key === "Enter" ? searchPost(event) : null
          }
        />
      </form>
    </div>
  );
}
